import { createStore } from "vuex";
import axios from "axios";
import router from "@/router";
import axiosConnector from "@/utils/axios-connector";
import axiosConnectorFormData from "@/utils/axios-connector-formData";

import tempJob from './modules/tempJob.js'
import job from './modules/job.js'
import growth from "./modules/growth.js";
import timeline from "./modules/timeline.js";
import search from "./modules/search.js";
import statistics from "./modules/statistics.js";

const baseURL = "https://i8a609.p.ssafy.io/api/user";
// const baseURL = "http://localhost:9090/user";

import { useToast } from "vue-toastification";
const toast = useToast()

export default createStore({

  state: {
    loginUser : {},
    sidebar: true,
    myInvitation: [],
  },
  getters: {},
  mutations: {
    LOGIN(state, payload) {
      state.loginUser = payload
      // console.log(payload)
    },
    LOG_OUT(state) {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("id");
      state.loginUser = {};
      state.myInvitation = [];
      router.push({name: 'MainPage'})
    },
    GET_USER(state, payload) {
      state.loginUser = payload;
      // console.log(state.loginUser);
      router.push({ name: "ProfilePage" });
    },
    GET_USER_ONLY(state, payload){
      state.loginUser = payload;
    },
    GET_MY_INVITATION(state, payload){
      state.myInvitation = payload
      router.push({ name: "MainPage" })
    },
    OPEN_SIDEBAR(state){
      state.sidebar = true;
    },
    CLOSE_SIDEBAR(state){
      state.sidebar = false;
    },
  },
  actions: {
    //  
    login({ commit, dispatch }, user) {
      const URL = `${baseURL}/login`;
      axios({
        url: URL,
        method: "POST",
        data: user,
      }).then((res) => {
        if (res.data.result == " ") { 
          toast.error("  .", {
            position: 'bottom-right',
          })
          return
        }          
        if (res.data.result == " ") { 
          toast.error("  .", {
            position: 'bottom-right',
          })
          return
        }
        sessionStorage.setItem("token", res.data["jwt-auth-token"]);
        sessionStorage.setItem('id', res.data.id)
        dispatch('getMyInvitation')
        commit("LOGIN", res.data);
      }).catch((err) => {
        console.log(err);
      });
    },
    //  
    signup(context, user) {
      const URL = `${baseURL}/regist`;
      axios({
        url: URL,
        method: "POST",
        data: user,
      }).then((res) => {
        // console.log(res.data)
        if(res.data == 'success'){
          router.push({ name: "UserSignupComplete" });
        }
      }).catch((err) => {
        console.log(err);
      });
    },

    //  
    logout({ commit }) {
      axiosConnector
        .post("user/logout")
        .then(() => {
          commit("LOG_OUT");
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //   => 
    // updataPassword.vue 

    //  
    sendPw(context, email) {
      const URL = `${baseURL}/sendPw`;
      axios({
        url: URL,
        method: "POST",
        params: { email: email },
      })
      .then(() => {
        console.log("");
        // commit("LOGIN_USER", res.data);
      })
      .catch((err) => {
        alert("!!!!!!!  ");
        console.log(err);
      });
    },

    //   
    // UserEmail.vue 

    //  
    //    :   (uploadImage)   (getUser) vuex 
    uploadImage({ dispatch }, formData) {
      axiosConnectorFormData
        .post("user/uploadImage", formData)
        .then((res) => { 
          if (res.data == "success") {
            dispatch("getUser");
          }
        })
        .catch((err) => { 
          console.log(err);
        })
    },

    uploadFile({ dispatch}, formData) {
      axiosConnectorFormData
        .post("user/uploadFile", formData)
        .then((res) => { 
          if (res.data == "success") {
            dispatch("getUser");
          }
          // console.log(res.data)
        })
        .catch((err) => { 
          console.log(err);
        })
    },
    
    //   
    //    :   (updateUser)   (getUser) vuex 
    updateUser({ dispatch }, user) { 
      axiosConnector
        .post("user", user)
        .then((res) => {
          if (res.data.result == " ") {
            return
          }       
          dispatch("getUser");
        })
        .catch((err) =>{ 
        // alert("    ~~")
          console.log(err);
        })
    },

    //   
    getUser({ commit }) {
      axiosConnector
        .get("user")
        .then((res) => {
          commit("GET_USER", res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // profile page   getUser
    getUserOnly({commit}){
      axiosConnector.get(`user`
      ).then((res)=>{
        commit('GET_USER_ONLY', res.data)
      }).catch((err)=>{
        console.log(err)
      })
    },

    //     
    getMyInvitation({commit}){
      axiosConnector.get(`growth/invitation`
      ).then((res)=>{
        // console.log(res.data)
        commit('GET_MY_INVITATION', res.data)
      }).catch((err)=>{
        console.log(err)
      })
    },

    //  
    acceptInvite({dispatch}, data){
      axiosConnector.put(`growth/invitation/${data.growthId}/${data.accept}`
      ).then(()=>{
          // console.log(res.data)
        dispatch('getMyInvitation')
      }).catch((err)=>{
          console.log(err)
      })
    },

    // sidebar 
    openSidebar({commit}){
      commit('OPEN_SIDEBAR');
    },
    // sidebar 
    closeSidebar({commit}){
      commit('CLOSE_SIDEBAR');
    },

  },

  modules: {
    growth: growth,
    timeline: timeline,
    search: search,
    job: job,
    tempJob: tempJob,
    statistics: statistics,
  },
});
